
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'

const list = () => ['', '', '']
export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur,
Tab,
TabItem,
XAddress
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
mounted () {
this.failureShowToast = false
this.changeSuccessShow = false
},
data () {
return {
title: 'Address',
showConfirmBox: false,
computeDaysValue: '',
isSubmited: false,
list2: list(),
index: 0,
value_0_1: [],
addressData: ChinaAddressV4Data,
realName: '',
phone: '',
address: '',
realAddress: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
showError: false,
addressError: '',
failureText: ''
}
},
methods: {
onCancelback () {

},
onPicIndexChange () {

},
goRedeemGift () {
this.$router.go(-1)
},
getName (value) {
return value2name(value, ChinaAddressV4Data)
},
goRedeemDetail () {
this.$router.push('setting')
},
goStruction () {
this.$router.push('setting')
},
goQuestion () {
this.showConfirmBox = true
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {

},
onConfirm () {
this.$router.push('question')
},
onShow () {

},
logHide () {

},
onHide () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onFocus (val, $event) {
console.log('on focus', val, $event)
},
onEnter (val) {
console.log('click enter!', val)
},
submitInfo () {
this.$vux.loading.show({
text: 'loading'
})
let userId = window.localStorage.getItem('userId')
this.address = this.getName(this.value_0_1)
this.$http.post(this.$apis.saveBaseInfo, {
userId: userId,
realName: this.realName,
phone: this.phone,
address: this.address,
realAddress: this.realAddress
}).then(res => {
if (res.code === 200) {
this.isSubmited = true
} else if (res.code === 500) {
this.addressError = res.msg
this.showError = true
}
this.$vux.loading.hide()
}).catch(function (error) {
console.log(error)
this.$vux.loading.hide()
})
}
}

}



